(function () {
    'use strict';

    angular
        .module('yesBankAngular.login')
        .factory('LoginService',
        ['Base64', '$http', '$rootScope','localStorageService',
            function (Base64, $http, $rootScope, localStorageService) {
                var service = {};
                service.successfulLogin = true;

                service.Login = function (data, callback) {
                    var authdata = Base64.encode(data.username + ':' + data.password);
                    var base64Conv = 'Basic ' + authdata;

                    var data = $http({
                        method: 'GET',
                        url: 'https://uatsky.yesbank.in/app/uat/BlockChainGateway/LoginService?role=' + data.role,
//                        url: 'https://10.0.45.87:1443/BlockChain2/LoginService?role=' + data.role,
                        headers: {
                            'Content-Type': 'application/json',
                            'X-IBM-Client-Id': '174524e5-cec9-4305-a147-2eb41a900dda',
                            'Authorization': base64Conv
                        },
                        xhrFields: {
                            withCredentials: true
                        },
                        crossDomain: true
                    });
                    return data;


                };

                service.SetCredentials = function (username, password, role) {

                    var authdata = Base64.encode(username + ':' + password);
                    $rootScope.globals = {
                        currentUser: {
                            username: username,
                            password:password,
                            role:role,
                            authdata: authdata
                        }
                    };
                    localStorageService.set('userObj',$rootScope.globals.currentUser);

                };

                service.ClearCredentials = function () {

                    localStorageService.clearAll();

                };

                return service;
            }])
})();

(function () {
    'use strict';

    angular
        .module('yesBankAngular.login')
        .controller('LoginController', LoginController);

    /** @ngInject */
    function LoginController($scope, $timeout, $state, localStorageService, urls, apiCallService, $rootScope, LoginService) {

        LoginService.ClearCredentials();
        $scope.successfulLogin = true;

        $scope.signUser = function (data) {

            LoginService.Login(data).then(function (result) {

                LoginService.SetCredentials(data.username, data.password, data.role);
                if (data.role == "ANCHOR") {
                    $state.go('app.anchor-sign-purchase-order');
                }
                if (data.role == "VENDOR") {
                    $state.go('app.vendor-sign-invoice');
                }
                if (data.role == "PAYMENT") {
                    $state.go('app.payment-initiation');
                }
                if (data.role == "BANK") {
                    $state.go('app.stats');
                }
                 if (data.role == "SETTLEMENT") {
                    $state.go('app.settlement');
                }

            }, function (result) {
                $scope.successfulLogin = false;
                $timeout(function() {
                  $scope.successfulLogin = true;
                }, 3000);
            });
        };

        $scope.closeErrorDiv = function(){
          $scope.successfulLogin = true;
        }
    }
})();

(function () {
  'use strict';

  angular
    .module('yesBankAngular.login')
      .factory('Base64', function () {
        /* jshint ignore:start */

        var keyStr = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=';

        return {
          encode: function (input) {
            var output = "";
            var chr1, chr2, chr3 = "";
            var enc1, enc2, enc3, enc4 = "";
            var i = 0;

            do {
              chr1 = input.charCodeAt(i++);
              chr2 = input.charCodeAt(i++);
              chr3 = input.charCodeAt(i++);

              enc1 = chr1 >> 2;
              enc2 = ((chr1 & 3) << 4) | (chr2 >> 4);
              enc3 = ((chr2 & 15) << 2) | (chr3 >> 6);
              enc4 = chr3 & 63;

              if (isNaN(chr2)) {
                enc3 = enc4 = 64;
              } else if (isNaN(chr3)) {
                enc4 = 64;
              }

              output = output +
                  keyStr.charAt(enc1) +
                  keyStr.charAt(enc2) +
                  keyStr.charAt(enc3) +
                  keyStr.charAt(enc4);
              chr1 = chr2 = chr3 = "";
              enc1 = enc2 = enc3 = enc4 = "";
            } while (i < input.length);

            return output;
          },

          decode: function (input) {
            var output = "";
            var chr1, chr2, chr3 = "";
            var enc1, enc2, enc3, enc4 = "";
            var i = 0;

            // remove all characters that are not A-Z, a-z, 0-9, +, /, or =
            var base64test = /[^A-Za-z0-9\+\/\=]/g;
            if (base64test.exec(input)) {
              window.alert("There were invalid base64 characters in the input text.\n" +
                  "Valid base64 characters are A-Z, a-z, 0-9, '+', '/',and '='\n" +
                  "Expect errors in decoding.");
            }
            input = input.replace(/[^A-Za-z0-9\+\/\=]/g, "");

            do {
              enc1 = keyStr.indexOf(input.charAt(i++));
              enc2 = keyStr.indexOf(input.charAt(i++));
              enc3 = keyStr.indexOf(input.charAt(i++));
              enc4 = keyStr.indexOf(input.charAt(i++));

              chr1 = (enc1 << 2) | (enc2 >> 4);
              chr2 = ((enc2 & 15) << 4) | (enc3 >> 2);
              chr3 = ((enc3 & 3) << 6) | enc4;

              output = output + String.fromCharCode(chr1);

              if (enc3 != 64) {
                output = output + String.fromCharCode(chr2);
              }
              if (enc4 != 64) {
                output = output + String.fromCharCode(chr3);
              }

              chr1 = chr2 = chr3 = "";
              enc1 = enc2 = enc3 = enc4 = "";

            } while (i < input.length);

            return output;
          }
        };

        /* jshint ignore:end */
      });
})();

